import React from "react";
import { CChart } from "@coreui/react-chartjs";

export default function Chart() {
    return (
        <>
            <div className="char-container d-flex flex-wrap ">
                <div className="col-lg-8">
                    <CChart
                        type="bar"
                        data={{
                            labels: ["January", "February", "March"],

                            datasets: [
                                {
                                    label: "Web Develop",
                                    backgroundColor: "#fd4485",
                                    hoverBackgroundColor: "#02126a",
                                    borderColor: "#02126a",
                                    barThickness: "50",
                                    data: [15, 20, 18],
                                },
                                {
                                    label: "Mobile App ",
                                    backgroundColor: "#ffa38d",
                                    hoverBackgroundColor: "#02126a",
                                    borderColor: "#02126a",
                                    barThickness: "50",
                                    data: [20, 10, 30],
                                },
                            ],
                        }}
                        labels="months"
                    />
                </div>
                <div className="section-title-container col-lg-4">
                    <div className="">
                        {" "}
                        <h1>Quarter Statistics</h1>
                    </div>
                </div>
            </div>
        </>
    );
}
